@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Ozone";
  src: url(/Clocks/static/media/Ozone.1abd8c1a.ttf);
  font-weight: 400;
  font-style: normal;
}

.App {
  background-color: darkgray;
  height: 100vh;
}

.topContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.timezoneForm {
  font-weight: bold;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.clocksTypesContainer {
  width: 15%;
  display: flex;
  justify-content: space-between;
}

.clockType {
  font-size: 1.5rem;
  padding: 0.7rem;
  border-radius: 8px;
}
.clockType:hover {
  color: white;
  background-color: LightSlateGray;
}

.clock-container {
  position: relative;
  width: 20%;
  overflow: hidden;
  background: #fff;
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.clock-container.styling {
  box-shadow: 0px 0px 10px #888888;
  border-radius: 50%;
  min-width: 100px;
  margin-top: 1%;
}
.clock-container:before {
  content: "";
  display: block;
  padding-top: 100%;
}

.clock-content {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.clock-content .background-numbers {
  position: absolute;
  width: 90%;
  height: 90%;
  top: 5%;
  left: 5%;
  z-index: 1;
}
.clock-content .background-numbers .numbers {
  fill: grey;
}
.clock-content .indicator {
  border-radius: 10px;
  transform-origin: bottom;
  outline: 1px solid transparent;
  position: absolute;
  bottom: 50%;
}
.clock-content .indicator.seconds-indicator {
  width: 0.5%;
  height: 37.5%;
  z-index: 2;
  background-color: #dfc48c;
}
.clock-content .indicator.minutes-indicator {
  width: 1%;
  height: 30%;
  z-index: 3;
  background-color: #cd6a51;
}
.clock-content .indicator.hours-indicator {
  width: 1.5%;
  height: 22.5%;
  z-index: 4;
  background-color: #8f9c6c;
}
.clock-content .indicator-cover {
  position: absolute;
  bottom: 48.5%;
  /*(50% - (3% /2)) */
  border-radius: 50%;
  width: 3%;
  height: 3%;
  background-color: lightgrey;
  z-index: 5;
}

.digital {
  margin-top: 50px;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 20px;
}

.digitalTime {
  font-family: "Ozone";
  color: white;
  font-size: 9rem;
  padding: 2rem;
  margin-top: 0.3%;
  text-align: center;
  background-color: black;
  border: 2px solid white;
  border-radius: 6px;
}

